.container {
  height: 250px;
  display: flex;
  min-width: 1060px; }

.avatar {
  height: 100%;
  opacity: 0.95; }

.infoContainer {
  width: calc(100vw - 250px);
  min-width: calc(1060px - 250px);
  display: flex;
  justify-content: space-between;
  padding-left: 30px; }

.briefDescriptionContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.whoamiContainer {}

.name {
  margin: 18px 0 6px;
  font-size: 36px;
  color: #f4f4f4;
  opacity: 0.98; }

.position {
  margin: 0;
  font-weight: 400;
  font-size: 24px;
  color: #e2e2e2; }

.contactsContainer {
  font-size: 18px;
  padding-bottom: 15px; }

.contactRow {
  margin-bottom: 13px;
  color: #dddddd; }

.contactIcon {
  font-size: 22px;
  color: #a9a9a9;
  margin: 0 7px -5px 0; }

.antiAdblockSocialsContainerClass {
  padding: 30px 35px 30px 0;
  display: flex;
  flex-direction: column; }

.antiAdblockSocialLink {
  color: white;
  margin-bottom: 17px;
  text-decoration: none;
  font-size: 19px;
  color: #eeeeee;
  opacity: 0.9; }

.socialIcon {
  color: #f9f9f9;
  font-size: 24px;
  margin: 0 8px -6px 0; }
