.container {
  width: 100%; }

.headline {
  margin: 0;
  padding: 20px 0 10px 30px; }

.horizontalLine {
  height: 2px;
  width: calc(100% - 23px * 2);
  margin-left: 23px; }

.skillsContainer {
  box-sizing: border-box;
  padding: 12px 32px;
  margin-top: 5px; }

.skillContainer {
  margin-bottom: 13px; }

.skillDescription {
  display: flex;
  justify-content: space-between; }

.skillName {
  margin: 0;
  font-size: 16px;
  color: #555555; }

.skillLevel {
  font-size: 15px;
  color: #666666; }

.skillProgressBar {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 7px;
  margin-top: 7px; }

.progressItemFull {
  box-sizing: border-box;
  border-width: 2px;
  border-style: solid;
  height: 16px;
  width: 13%;
  transform: skew(-30deg);
  opacity: 0.9; }

.progressItemEmpty {
  @extend .progressItemFull;
  background-color: transparent; }
