.container {
  width: 100%; }

.headline {
  margin: 10px 0 0 0;
  padding: 20px 0 10px 30px; }

.horizontalLine {
  height: 2px;
  width: calc(100% - 23px * 2);
  margin-left: 23px; }

.educationInfoContainer {
  box-sizing: border-box;
  padding: 10px 0 0 25px; }

.schoolName {
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
  color: #444444; }

.faculty {
  color: #595959;
  font-size: 18px;
  line-height: 26px; }

.degreeDesc {
  color: #595959;
  font-size: 18px;
  line-height: 26px; }
