.container {
  width: 100%; }

.headline {
  margin: 0;
  padding: 20px 0 10px 30px; }

.horizontalLine {
  height: 2px;
  width: calc(100% - 23px * 2);
  margin-left: 23px; }

.experiencesContainer {
  box-sizing: border-box;
  padding: 5px 0 0 25px; }

.experienceItem {
  display: flex;
  margin-top: 13px; }

.experienceItemDate {
  min-width: 120px;
  font-size: 20px;
  font-weight: 500;
  color: #444444; }

.experienceItemFrom {}

.experienceItemTo {}

.experienceItemDesc {
  font-size: 18px;
  color: #222222; }
