.container {
  width: 100%; }

.headline {
  margin: 10px 0 0 0;
  padding: 20px 0 10px 30px; }

.horizontalLine {
  height: 2px;
  width: calc(100% - 23px * 2);
  margin-left: 23px; }

.skillsContainer {
  box-sizing: border-box;
  padding: 10px 0 0 25px;
  display: flex;
  justify-content: flex-start; }

.listColumnContainer {
  width: 48%;
  box-sizing: border-box; }

.listContainer {
  list-style: none;
  margin: 0;
  padding-left: 5px; }

.listItem {
  font-size: 20px;
  line-height: 34px;
  color: #555555;
  font-weight: 500; }

.listItem::before {
  content: "‚óè";
  padding-right: 10px; }
